import React from "react";
import MenuPoint from "./MenuPoint/MenuPoint";
import shortid from "shortid";

const Menu = () => {
  const menuLayout = [
    {
      id: shortid.generate(),
      name: "Category",
      items: ["Breakfast", "Desserts", "Dinners", "Lunch"]
    },
    {
      id: shortid.generate(),
      name: "Origin",
      items: ["Chinese", "Indian", "Italian", "Mexican"]
    },
    {
      id: shortid.generate(),
      name: "Difficulty",
      items: ["Easy", "Medium", "Hard"]
    }
  ];

  return menuLayout.map(menuItem => (
    <div key={menuItem.id}>
      <MenuPoint name={menuItem.name} items={menuItem.items} />
    </div>
  ));

};

export default Menu;
